import React from 'react'
import Layout from '../components/Layout'
import { graphql, useStaticQuery } from 'gatsby'
import { getImage } from "gatsby-plugin-image"
import { BgImage } from "gbimage-bridge"

export default function Doneer() {
    const { placeholderImage } = useStaticQuery(
        graphql`
          query {
            placeholderImage: file(relativePath: {eq: "Doneer.jpg"}) {
              childImageSharp {
                gatsbyImageData(
                  width: 1600
                  quality: 80
                  webpOptions: {quality: 90}  
                  formats: [AUTO, WEBP, AVIF]
                )
              }
            }
          }
          
        `
      )
      const pluginImage = getImage(placeholderImage);
    
    return (
        <Layout>
             <BgImage image={pluginImage} className="bg"
      Tag="section">
            </BgImage>
        <div className="genText">
            <p>
            Als je je aangetrokken voelt tot onze missie en financieel wilt bijdragen 
            dan nemen wij het dankbaar in ontvangst. Met een financiële bijdrage open 
            je niet alleen de deur voor jezelf, maar ook voor vele anderen.
            </p>
            <p>
            Jouw bijdrage wordt gebruikt om het gedachtegoed van Aikyam om te zetten 
            in concrete vormen.
            </p>
            <p>
            Op den duur zal dat zijn in de vorm van een groeicentrum en leefgemeenschap 
            waar iedereen welkom is om in een liefdevolle en harmonieuze atmosfeer nader 
            tot zichzelf en tot elkaar te kunnen komen. Een plek voor heling en groei, 
            voor jong en oud, dat is wat Aikyam beoogt.
            </p>
            <p>
            De naam Aikyam (spreek uit ‘Ekyam’) is met zorg gekozen en komt uit het Sanskriet, 
            de oudst levende Indo-Europese taal:<br/>
            - ‘Aik’ of ‘ek’ betekent één<br/>
            - ‘Yam’ is de zaad mantra die het hart chakra opent
            </p>
            <p className="centerText">
            Aikyam betekent Eenheid, Harmonie.
            </p>
            <p className="centerText">
            Je kunt je bijdrage overmaken op IBAN NL37TRIO0320185605 op naam van Stichting Aikyam.
            </p>
            <div className="centerImage">
                <img src="namaste.png"/>
            </div>
        </div>
        </Layout>
    )
}
